/* -------------------------------------
		GLOBAL
------------------------------------- */
@import 'construct/vars';

$font                 : "Helvetica Neue", "Helvetica", Helvetica, Arial, sans-serif;
$font-title           : "HelveticaNeue-Light", "Helvetica Neue Light", "Helvetica Neue", Helvetica, Arial, "Lucida Grande", sans-serif;
$font-color           : #333333;
$font-color-highlight : $gel_green;
$background-image     : '/assets/images/email-background.jpg';

* {
  margin:0;
  padding:0;
}

* { font-family: $font }

img {
  max-width: 100%;
}

.collapse {
  margin:0;
  padding:0;
}

html, body {
  width: 100% !important;
  height: 100%;
  margin:0;
  padding:0;
}

.body {
  -webkit-font-smoothing:antialiased;
  -webkit-text-size-adjust:none;
  width: 100% !important;
  height: 100%;
  padding:0;
  background-color:#fff;
  background-image:url($background-image);
  background-repeat: no-repeat;
  background-position:center center;
  background-size:100% 100%;
  background-size:cover;
  margin:0px !important;

  > tr {
    padding:0;
    margin:0;

    > td {
      padding:$spacing*2 0;
      margin:0;
      vertical-align: text-top;
    }
  }
}

/* -------------------------------------
        ELEMENTS
------------------------------------- */
a {
  color: $font-color-highlight !important;
  text-decoration:none !important;
}

.btn {
  text-decoration:none;
  color: #FFF;
  background-color: #666;
  padding:10px 16px;
  font-weight:bold;
  margin-right:10px;
  text-align:center;
  cursor:pointer;
  display: inline-block;
}

p.callout {
  padding:15px;
  background-color:#ECF8FF;
  margin-bottom: 15px;
}
.callout a {
  font-weight:bold;
  color: $font-color-highlight;
}

.outer_bg {
  background:#fff;
  max-width:600px!important;
  margin:0 auto!important; /* makes it centered */
}

/* -------------------------------------
        HEADER
------------------------------------- */
table.head-wrap {
  width: 100%;
  padding:10px 5px 0;

  .header.container table  {
    text-align:center;

    .image_logo {
      width:100%;
      max-width:100%;
      height:auto;
    }
  }
}


/* -------------------------------------
        BODY
------------------------------------- */
table.body-wrap {
  width: 100%;
  padding:0;

  .content {
    table {
      tr {
        th {
          padding:0;
        }

        td {
          text-align:left;
          padding:10px 10px 0;
          line-height:1.6;
          font-family: $font !important;
        }
      }

      tfoot {
        tr {
          td {
            padding:10px 0 0;
          }
        }
      }
    }
  }
}

/* -------------------------------------
        FOOTER
------------------------------------- */
table.footer-wrap {
  width: 100%;
  clear:both!important;

  .container td.content p {
    border-top: 1px solid rgb(215,215,215);
    padding-top:15px;
    font-size:8px;
    font-weight: bold;
  }

  p {
    font-size:12px;
  }
}

/* -------------------------------------
        TYPOGRAPHY
------------------------------------- */
h1,h2,h3,h4,h5,h6 {
  font-family: $font-title; line-height: 1.1; margin-bottom:15px; color:$font-color-highlight;
}
h1 small, h2 small, h3 small, h4 small, h5 small, h6 small { font-size: 60%; color: $font-color-highlight; line-height: 0; text-transform: none; }

h1 { font-weight:200; font-size: 44px;}
h2 { font-weight:200; font-size: 37px;}
h3 { font-weight:500; font-size: 27px;}
h4 { font-weight:500; font-size: 23px;}
h5 { font-weight:900; font-size: 17px;}
h6 { font-weight:900; font-size: 14px;}

.collapse { margin:0!important;}

p, ul {
  font-family: $font !important;
  margin-bottom: 10px;
  font-weight: normal;
  font-size:14px;
  line-height:1.6;
  color:$font-color;

  strong {
    font-family: $font !important;
  }
}
p.lead { font-size:17px; }
p.last { margin-bottom:0px;}

ul li {
  margin-left:5px;
  list-style-position: inside;
}

p a {
  color:$font-color-highlight;
  font-weight:600;
}

/* -------------------------------------
        SIDEBAR
------------------------------------- */
ul.sidebar {
  background:#ebebeb;
  display:block;
  list-style-type: none;
}
ul.sidebar li { display: block; margin:0;}
ul.sidebar li a {
  text-decoration:none;
  color: $font-color-highlight;
  padding:10px 16px;
  /* 	font-weight:bold; */
  margin-right:10px;
  /* 	text-align:center; */
  cursor:pointer;
  border-bottom: 1px solid #777777;
  border-top: 1px solid #FFFFFF;
  display:block;
  margin:0;
}
ul.sidebar li a.last { border-bottom-width:0px;}
ul.sidebar li a h1,ul.sidebar li a h2,ul.sidebar li a h3,ul.sidebar li a h4,ul.sidebar li a h5,ul.sidebar li a h6,ul.sidebar li a p { margin-bottom:0!important;}



/* ---------------------------------------------------
        RESPONSIVENESS
        Nuke it from orbit. It's the only way to be sure.
------------------------------------------------------ */

/* Set a max-width, and make it display as block so it will automatically stretch to that width, but will also shrink down on a phone or something */
.container {
  display:block!important;
  max-width:600px!important;
  margin:0 auto!important; /* makes it centered */
  clear:both!important;
}

/* This should also be a block element, so that it will fill 100% of the .container */
.content {
  padding:0px;
  max-width:600px;
  margin:0 auto;
  display:block;
}

/* Let's make sure tables in the content area are 100% wide */
.content table { width: 100%; }


/* Odds and ends */
.column {
  width: 300px;
  float:left;
}
.column tr td { padding: 15px; }
.column-wrap {
  padding:0!important;
  margin:0 auto;
  max-width:600px!important;
}
.column table { width:100%;}

/* Be sure to place a .clear element after each set of columns, just to be safe */
.clear { display: block; clear: both; }

/* -------------------------------------------
        PHONE
        For clients that support media queries.
        Nothing fancy.
-------------------------------------------- */
@media only screen and (max-width: 600px) {

  a[class="btn"] { display:block!important; margin-bottom:10px!important; background-image:none!important; margin-right:0!important;}

  div[class="column"] { width: auto!important; float:none!important;}
}
